@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #ffffff;
  min-height: 100vh;
  text-align: center;
}

body{
  text-align: justify;
  text-overflow: ellipsis;
  font-family: 'Open Sans', sans-serif;
}

.title h1{
  font-family: 'Montserrat', sans-serif;
  font-size: 28px;
  margin: 50px 0 0 0;
}
.button-title {
    width: 100%;
    height: 100%;   
    background: none; 
    border: none;
  }

  .button-container{
    width: 200px;
    height: 50px;
    margin: auto;
    background-color: rgb(173, 216, 230);
    border-radius: 8px;
  }

  @media only screen and (max-width: 520px) {
    .button-container {
      background-color: rgb(173, 216, 230);
    }
  }

.home-container {
    width: 50vw;
    margin: 50px auto;
}

@media only screen and (max-width: 370px) {
    .home-container {
        width: 70vw;
    }
  }

.child-box {
    width: 200px;
    height: 200px;
    background-color: #FFFFFF;
    border-radius: 5px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  box-shadow: 0px 0px 4px 1px rgba(0,0,0,0.75);
  }

.child-box:hover {
    cursor: pointer;
    /* background-color: #eff0f1; */
}

.active {
    /* background-color: rgba(138, 43, 226, 0.5);; */
    outline: 100px solid rgba(255, 0, 0, 0.5) !important;
    outline-offset: -100px;
    overflow: hidden;
    position: relative;
    height: 200px;
    width: 200px;
}

.disable {
    pointer-events: none;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    padding: 0 10px 15px 10px;
}

.child-box img{
    width: 100px;
    display: block;
    margin: 20px auto 20px auto;
}

.child-box p{
    text-align: center;
}

select{
    pointer-events: all;
    border: 1px solid #e4e4e4;
    border-radius: 6px;
    padding: 5px;
}

@media only screen and (max-width: 800px) {
    .child-box {
        width: 175px;
      }
      .disable {
        padding: 0;
    }
  }

  @media only screen and (max-width: 400px) {
    .child-box {
        width: 150px;
      }
  }
.box-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 35px;
    gap: 35px;
    place-items: center;
    width: 75vw;
    margin: 10px auto 50px auto;
}

.planet-container{
    padding-bottom: 15px;
}

.planet-container p{
    text-align: center;
}

@media only screen and (max-width: 800px) {
    .box-container {
        grid-template-columns: repeat(2, 1fr);
    }
  }

  @media only screen and (max-width: 500px) {
    .box-container {
        grid-template-columns: repeat(2, 1fr);
        margin: 10px auto 50px auto;
        width: 100vw;
        padding: 0 10px;
    }
  }

.vehicle-container p{
    text-align: center;
}

.vehicle-container {
    padding-bottom: 15px;
}
.status-container {
    width: 65vh;
    margin: 0 auto;
}

@media only screen and (max-width: 800px) {
    .status-container {
        width: 90%;
        margin: 0 auto;
    }
  }
